@activeBackground: rgb(88, 88, 88);
@activeContent: rgb(255, 255, 255);

.itemWrapOpen {
	background: @activeContent;
	color: @activeContent;
	border-left: 3px solid;
	box-shadow: 0 6px 18px 0 rgb(54 55 64 / 10%);
}

.mainItemSecond {
	font-size: 13px;
	background: none;
	border: none;
	box-shadow: none;
	cursor: pointer;
}

.chevron {
	position: absolute;
	right: 22px;
	transition: opacity 0.5s linear;
	opacity: 1;
}

.chevronHidden {
	right: 10px;
}

.itemWrap {
	color: @activeContent;
	svg {
		margin-left: 30px;
	}
	&:hover {
		background: @activeBackground;
		color: @activeContent;
		border-left: 3px solid;
		box-shadow: 0 6px 18px 0 rgb(54 55 64 / 10%);
	}
}

.hide {
	display: none;
}

@primary-color: #1DA57A;